.reviewBar {
  width: 350px;
  height: 150px;
  background: #ffffff80;
  display: flex;
  flex-direction: row;
  color: black;
  padding: 20px;
  cursor: pointer;
  margin: 15px;
}
.reviewBar :hover {
  box-shadow: none;
}
.reviewsBar__content {
  display: flex;
  /* justify-content: space-between; */
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
}
.reviewsBar__content__name {
  font-size: 1.2rem;
  font-weight: 900;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  width: 100%;
  margin-bottom: 10px;
  padding-right: 30px;
}
.reviewsBar__content__name p {
  margin-bottom: 0px;
}
.reviewsBar__content__name__reviews {
  font-size: 1rem;
  color: #e75a5a;
  display: flex;
  flex-direction: row;
}
.reviewsBar__content__review {
  color: #00000090;
}
@media screen and (max-width: 900px) {
  .reviewBar {
    width: 350px;
    height: max-content;
    background: #ffffff80;
    display: flex;
    flex-direction: row;
    color: black;
    padding: 15px;

    cursor: pointer;
    margin: 8px;
  }
  .reviewBar :hover {
    box-shadow: none;
  }
  .reviewsBar__image {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 7px;
  }
  .reviewsBar__content {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: column;
  }
  .reviewsBar__content__name {
    font-size: 1rem;
    font-weight: 900;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    width: 100%;
    padding-right: 5px;
  }
  .reviewsBar__content__name p {
    margin-bottom: 0px;
    text-align: start;
  }
  .reviewsBar__content__name__reviews {
    font-size: 1rem;
    color: #e75a5a;
    display: flex;
    flex-direction: column;
    /* margin-right: 20px; */
    justify-content: center;
    align-items: center;
  }
  .reviewsBar__content__review {
    color: #00000090;
  }
}
