.addingReview {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  padding: 20px;
  margin: 20px;
}
.addingReview input,
.addingReview textarea,
.addingReview select {
  padding: 15px;
  margin: 10px;
  border: none;
  outline: none;
  background: #00000010;
  width: 90%;
}
.addingReview__rating {
  padding: 20px;
}
.addingReview__rating p {
  font-weight: 900;
}
