/* ************************************************************************************************** */
/* LoginRegister */
.loginRegister {
  min-height: 100vh;
  padding-top: 50px;
  font-family: "Courier New", Courier, monospace;
}
.loginRegister__block {
  width: 500px;
  margin-top: 50px;
  height: max-content;
  margin-left: auto;
  margin-right: auto;
  background: #fff;
}
.loginRegister__block__buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  height: 50px;
}
.loginRegister__block__buttons p {
  width: 50%;
  border: none;
  outline: none;
  background: none;
  font-size: 1.2rem;
  margin-bottom: 0px;
  text-align: center;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.loginRegister__block__content {
  padding: 20px;
}
.loginRegister__block__content__login__text,
.loginRegister__block__content__register__text {
  font-size: 3rem;
  text-align: center;
  font-weight: 900;
  margin-bottom: 0px;
  color: #000000;
}
.loginRegister__block__content__login form,
.loginRegister__block__content__register form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.loginRegister__block__content__login form input,
.loginRegister__block__content__register form input {
  width: 60%;
  padding: 15px;
  margin: 10px;
  border: none;
  outline: none;
  background: #00000015;
  font-family: "Courier New", Courier, monospace;
  font-weight: 800;
}
.loginRegister__block__content__login form button,
.loginRegister__block__content__register form button {
  background: #f78765;
  padding: 15px;
  margin: 20px;
  margin-bottom: 30px;
  border: none;
  outline: none;
  width: 200px;
  font-size: 1.4rem;
  color: white;
  font-family: "Courier New", Courier, monospace;
  font-weight: 900;
  cursor: pointer;
}
@media screen and (max-width: 500px) {
