/* ************************************************************************************************** */
/* HomePage */
.home {
  min-height: 70vh;
  max-height: max-content;
  display: flex;
  flex-direction: row;
  text-align: center;
  margin-top: 30px;
}
.home__left {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  padding: 20px;
  font-family: "Merienda", cursive;
  font-size: 1.3rem;
  line-height: 2rem;
  color: #a12e49;
}
.home__left__ButtonContent .button {
  font-size: 1.2rem;
  font-family: "Merienda", cursive;
  background: #fcf0e6;
  border: #f53965 1.5px solid;
  border-radius: 30px;
  outline: none;
  color: #f53965;
  padding: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  cursor: pointer;
}
.home__left__ButtonContent :hover {
  background: #f5798970;
  color: white;
  border: white 1.5px solid;
}
.home__right {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  width: 100%;
  animation: MoveUpDown 3s linear infinite;
  position: relative;
  z-index: 1000;
}
@keyframes MoveUpDown {
  0%,
  100% {
    bottom: 0;
  }
  50% {
    bottom: 50px;
  }
}
@media screen and (max-width: 900px) {
  .home {
    height: max-content;
    display: flex;
    flex-direction: column-reverse;
    text-align: center;
    margin-top: 30px;
  }
