    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    padding: 20px;
    font-family: "Merienda", cursive;
    font-size: 1.1rem;
    line-height: 2rem;
    color: #a12e49;
    margin-bottom: 50px;
  }
  .home__left__ButtonContent button {
    font-size: 1.2rem;
    font-family: "Merienda", cursive;
    background: #fcf0e6;
    border: #f53965 1.5px solid;
    border-radius: 30px;
    outline: none;
    color: #f53965;
    padding: 15px;
    cursor: pointer;
  }

  .home__right {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    width: 100%;
