/* contactPage */
.contact {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fca6a6;
  flex-direction: row;
}
.contact__content {
  width: 450px;
  height: 450px;
  background: #fff;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  margin-left: 0px;
  margin-right: auto;
}
.contact__content__heading {
  margin-top: 30px;
}
.contact__content__heading p {
  font-size: 2rem;
  font-weight: 900;
}
.contact__content__description p {
  font-size: 0.9rem;
  font-weight: 400;
}
.contact__content__content form {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.contact__content__content input,
.contact__content__content textarea {
  border: none;
  outline: none;
  padding: 15px;
  background: #f5698a20;
  margin: 10px;
  padding-left: 20px;
  width: 300px;
  color: #00000075;
}
.contact__content__content form button {
  padding: 20px;
  border-radius: 30px;
  margin-top: 20px;
  border: none;
  outline: none;
  background-color: #f53965;
  width: 200px;
  color: white;
  cursor: pointer;
  font-weight: 900;
  font-size: 1rem;
}
.contact__content__contact {
  width: 300px;
  height: 350px;
  background: #f87b7b;
  padding: 7px;
  color: white;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  margin-left: auto;
  margin-right: 0px;
}
.contact__content__contact__heading {
  width: 100%;
  font-size: 1.5rem;
  text-align: center;
  margin-top: 10px;
  font-weight: 900;
}
.contact__content__contact__details {
  padding: 20px;
  margin-top: -20px;
}
.contact__content__contact__details__box {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
}
.contact__content__contact__details__box__name {
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
}
.contact__content__contact__socialMedia {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}
